.App {
  text-align: center;
}

.App-logo {
  height: 50vmin;
  margin-top: 10vh;
  pointer-events: none;
}

.App-header {
  background-color: #3a0247;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* justify-content: center;  */
  font-size: calc(10px + 1vmin);
  color: white;
}

.App-link {
  color: #5cffb0;
  animation: colorTransition 2s infinite alternate;
} 

@keyframes colorTransition {
  0% {
    color: #5cffb0;
  }
  100% {
    color: #fff;
  }
} 
